/*Pixels to rems*/ /* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  font-family: "Poppins", sans-serif;
  background: url("/images/bg-intro-mobile.png") center center/cover;
  background-color: var(--Red);
}
@media (min-width: 87.5em) {
  body {
    background: url("/images/bg-intro-desktop.png");
    background-color: var(--Red);
  }
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
:root {
  --Red: hsl(0, 100%, 74%);
  --Green: hsl(154, 59%, 51%);
  --Blue: hsl(248, 32%, 49%);
  --Dark-Blue: hsl(249, 10%, 26%);
  --Grayish-Blue: hsl(246, 25%, 77%);
}

.header {
  text-align: center;
  margin-bottom: 4.3125rem;
  margin-top: 6.375rem;
  max-width: 400px;
}
@media (min-width: 87.5em) {
  .header {
    grid-column: 1/2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.header__title {
  font-size: 1.375rem;
  font-size: clamp(1.375rem, 1.1549295775rem + 0.9389671362vw, 2rem);
  color: #fff;
  display: block;
}
.header :nth-child(2) {
  margin-bottom: 2.375rem;
}
.header__text {
  display: block;
  font-size: 0.75rem;
  color: #fff;
  margin: 0 2.0625rem;
}

.main {
  text-align: center;
}
@media (min-width: 87.5em) {
  .main {
    grid-column: 2/3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
.main__btn {
  margin: 0 1.5rem 1.5rem 1.5rem;
  width: 20.625rem;
  height: 5.625rem;
  background: var(--Blue);
  font-size: 0.8125rem;
  border: none;
  border-radius: 0.625rem;
  color: white;
  box-shadow: 5px 5px 5px rgba(216, 58, 58, 0.25);
}
.main__btn span {
  font-weight: bold;
}

.form__wrapper {
  display: flex;
  justify-content: center; /* align horizontal */
  align-items: center; /* align vertical */
}

.form {
  padding: 1.5rem;
  border-radius: 0.625rem;
  background: #fff;
  width: 20.625rem;
}
.form__first {
  position: relative;
}
.form__last {
  position: relative;
}
.form__email {
  position: relative;
}
.form__password {
  position: relative;
}
.form__btn-btn {
  border: none;
  height: 3.4375rem;
  width: 100%;
  background: var(--Green);
  box-shadow: 3px 3px 3px var(--Green);
  border-radius: 0.375rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.form__text {
  font-size: 0.625rem;
  color: var(--Grayish-Blue);
  text-align: center;
  margin: 17px 30px 0 30px;
}
.form__text span {
  color: var(--Red);
}

.input {
  border: 1px solid var(--Grayish-Blue);
  border-radius: 0.375rem;
  height: 3.4375rem;
  width: 100%;
  margin-bottom: 1rem;
  padding-left: 1.25rem;
}
.input::placeholder {
  font-size: 0.75rem;
  font-weight: 600;
}
.input:focus-visible:invalid {
  border: 5px solid var(--Red);
}
.input:focus-visible:invalid ~ p {
  display: block;
}
.input:focus-visible:invalid ~ img {
  display: block;
}
.input__invalid {
  display: none;
  position: absolute;
  bottom: 0;
  right: 0;
  padding-top: -10px;
  text-align: right;
  font-size: 0.625rem;
  color: var(--Red);
}
.input__invalid-icon {
  display: none;
  position: absolute;
  right: 0;
  top: 50%;
  translate: -50% -85%;
}

.wrapper {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
}
@media (min-width: 87.5em) {
  .wrapper {
    grid-template-columns: 1fr 1fr;
    height: 100vh;
  }
}